import {
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CRow,
  CBadge,
} from "@coreui/react-pro";
import React, { useEffect, useState } from "react";
import NavBar from "../../navbars/ev_super_admin/nav_normal_school";
import EDU_AXIOS from "src/helpers/edu_axios";
import MaterialTable from "material-table";
import { faUser, faTrash, faEye } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useHistory } from "react-router-dom";
function time_table_list(props) {
  let school_id = props.match.params.school_id;
  let history = useHistory();
  let [time_table, setTimeTable] = useState([]);

  useEffect(() => {
    timeTableList();
  }, []);

  const timeTableList = () => {
    let url =
      process.env.REACT_APP_API_PATH +
      "/ttm/get-time-table-list-by-school/" +
      school_id;
    EDU_AXIOS.get(url)
      .then((resp) => {
        setTimeTable(resp.data.data);
      })

      .catch((error) => {
        console.log(error);
      });
  };

  const deleteTimeTable = (id) => {
    let url = process.env.REACT_APP_API_PATH + "/ttm/delete-time-table/" + id;
    if (confirm("Are you sure you want to delete this record?")) {
      // Save it!
      EDU_AXIOS.delete(url)
        .then((resp) => {
          timeTableList();
        })

        .catch((error) => {
          console.log(error);
        });
    }
  };
  const columns = [
    {
      title: "Group Name",
      field: "group",
    },
    {
      title: "Start Time",
      field: "start_time",
    },
  ];
  return (
    <CCardGroup>
      <CCard className="p-2">
        <CCardBody>
          <CRow>
            <CCol>
              <NavBar id={school_id} />
            </CCol>
          </CRow>
          {/*Table Start*/}
          <MaterialTable
            title={"Time Table List"}
            data={time_table}
            columns={columns}
            onSelectionChange={(rows) => setSelectedRows(rows)}
            options={{
              search: true,
              paging: true,
              filtering: false,
              exportButton: true,
              rowStyle: (x) => {
                if (x.tableData.id % 2) {
                  return { backgroundColor: "#f9f9f9" };
                }
              },
            }}
            actions={[
              {
                icon: () => (
                  <FontAwesomeIcon
                    icon={faTrash}
                    style={{ fontSize: "1rem" }}
                  />
                ),
                tooltip: "Delete Record",
                onClick: (event, rowData) => {
                  deleteTimeTable(rowData._id);
                },
              },
            ]}
          />
          {/*Table End* */}
        </CCardBody>
      </CCard>
    </CCardGroup>
  );
}

export default time_table_list;
